				5: '–Ω–æ—Ä–º–∏–∫, —Ç–æ–ª—å–∫–æ —á—Ç–æ –º–æ–ª–æ–∫–æ –Ω–∞ –∫—É—Ö–Ω–µ –±—ã–ª–æ —Å–ø–∞—Å–µ–Ω:)',
