				2: '–Ω—É –¥–∞–≤–∞–π –µ—â–µ —Ä–∞–∑ –ø–æ—Å–ø—Ä–∞—à–∏–≤–∞–µ—Ç –∫–∞–∫ –º—ã))',
				3: '–¥–∞ –∏ —è –∫–æ–Ω–µ—á–Ω–æ —Ç–æ–∂–µ –∑–∞ –ø–∞—Ä—É –º–∏–Ω—É—Ç –∑–∞–±—ã–ª –∫–∞–∫ —É —Ç–µ–±—è –¥–µ–ª–∞;)',
